@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*  Home page style  */

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #252734;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ffaf29;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ACADB2;
}

.homePageContainer {
  width: 100%;
}

.header {
  background-color: #333646;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  width: 100%;
}

.headerContent {
  width: 60%;
  display: flex;
  justify-content: center;
}

.columnOneHeader {
  color: #EBEBEC;
  width: 50%;
  text-align: start;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.nameTitle {
  font-size: 66px;
  margin: -10px 0;
  font-weight: bold;
  letter-spacing: 0.2rem;
}

.separatorHeader {
  border: none;
  height: 7px;
  width: 50px;
  background-color: #FFAF29;
  margin: 15px 0 0 0;
}

.iconContainer {
  display: flex;
  justify-content: flex-start;
  margin: 75px 0 0 0;
}

.iconHeader {
  transition: ease-in-out 0.2s;
}

.linkIconHeader:hover {
  transform: scale(1.1);
}
.linkIconHeader:hover .iconHeader {
  color: #ffaf29;
}

.columnTwoHeader {
  text-align: start;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 50%;
}

.introHeader {
  width: 60%;
  color: #ACADB2;
  margin: 10px 0;
}

.subtitleHeader {
  margin: 0 0 15px 0;
  color: #EBEBEC;
  width: 60%;
}

.subtitleHeaderText {
  color: #ACADB2;
  width: 60%;
}

/*  About section   */

.aboutContainer {
  background-color: #252734;
  width: 100%;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 10vh;
}

.aboutContent {
  width: 60%;
  display: flex;
  justify-content: center;
}

.columnAboutContainer {
  display: flex;
  text-align: start;
  justify-content: center;
}

.firstColumnAbout {
  width: 50%;
}

.secColumnAbout {
  width: 50%;
}

.expProjectsContainer {
  display: flex;
  align-items: flex-end;
  height: 80px;
}

.numberExpProject {
  color: #FFAF29;
  font-size: 58px;
  margin: 0;
}

.textExpProject {
  color: #EBEBEC;
  margin: 0 30px 15px 10px;
}


/*  Projects  */

.projects {
  background-color: #333646;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 130vh;
}

.projectsContent {
  width: 60%;
}

.projectsSquareContainer {
  display: flex;
  margin-top: -130px;
  justify-content: space-between;
  width: 100%;
}

.projectsSquare {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  width: 300px;
  height: 300px;
  background-color: #424657;
  color: #EBEBEC;
  box-sizing: border-box;
  padding: 45px 15px;
  font-family: 'Open Sans', sans-serif;
  text-align: start;
}

.iconTextProjectSquare {
  display: flex;
  width: 200px;
}

.projectSquareTitle {
  font-size: 36px;
  margin: 10px 0;
}

.projectSquareTextContainer {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  margin-left: 25px;
}

.orangeSquare {
  background-color: #FFAF29;
  color: #FFF7EA;
}

/*  Selected projects */

.selectedProjects {
  margin-top: 25px;
  display: flex;
  height: 100vh;
}

.selectedProjectsContent {
  width: 100%;
  display: flex;
}

.firstColumnSelectedProjects {
  width: 50%;
  text-align: start;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.projectsLastMonthsText {
  width: 300px;
}

.firstColumnSelectedProjects h2 {
  color: #EBEBEC;
  margin: 10px 0;
}

.firstColumnSelectedProjects p {
  color: #ACADB2;
}

.secondColumnSelectedProjects {
  width: 50%;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  justify-content: space-around;
}

.projectCard {
  background-color: #252734;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 300px;
  width: 450px;
  color: #EBEBEC;
  font-family: 'Open Sans', sans-serif;
  transition: ease-in-out 0.3s;
  cursor: pointer;
}
.projectCard:hover {
  transform: scale(1.3);
}
.projectCard:hover .clickToSeeProject {
  display: inline;
  display: initial;
}

.projectCardText {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 20%;
  justify-content: space-around;
  align-items: center;
  text-align: start;
}

.projectCardImage {
  width: 100%;
  height: 250px;
}

.clickToSeeProject {
  display: none;
  text-align: center;
  margin: 5px 0;
  box-sizing: border-box;
  transition: ease-in-out 600ms;
  color: #EBEBEC;
  background-color: #252734;
}

.cardBeers {
  margin-top: 150px;
}

/*  Footer  */

footer {
  background-color: #252734;
  color: #EBEBEC;
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: start;
}

.footerContent {
  width: 60%;
  display: flex;
  justify-content: space-between;
}

.firstColumnFooter {
  width: 50%;
}

.secondColumnFooter {
  width: 50%;
  text-align: end;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}

.footerIconsContainer {
  margin: 10px 0;
  display: flex;
  justify-content: space-around;
  width: 20%;
}

.iconFooter {
  transition: ease-in-out 100ms;
}

footer a .iconFooter:hover {
  color: #FFAF29;
}

.footerSeparator {
  width: 35px;
  height: 3px;
  border: none;
  background-color: #FFAF29;
  margin: 10px 10px;
}


/*  Responsive  */

@media (max-width: 1060px) {
  .header {
    height: auto;
    padding: 50px 25px;
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
  }

  .reactIconHeader {
    display: none;
  }

  .headerContent {
    flex-direction: column;
    width: 80%;
  }

  .iconContainer {
    margin: 65px 0 0 0;
  }

  .columnOneHeader p {
    font-size: 46px;
  }

  .subtitleHeader {
    width: 100%;
  }

  .columnTwoHeader {
    margin-top: 25px;
    width: 100%;
    align-items: flex-start;
  }

  /*  About   */
  .aboutContainer {
    height: auto;
    padding: 50px 25px ;
    box-sizing: border-box;
  }

  .aboutContent {
    flex-direction: column;
    width: 80%;
  }

  .columnAboutContainer {
    flex-direction: column;
  }

  .firstColumnAbout {
    width: 100%;
  }

  .secColumnAbout {
    width: 100%;
  }

  .numberExpProject {
    font-size: 42px;
  }

  .textExpProject {
    margin: 5px;
  }

  /*  Projects  */

  .projects {
    height: auto;
    padding: 50px 25px;
    box-sizing: border-box;
  }

  .selectedProjects {
    height: auto;
  }

  .projectsLastMonthsText {
    width: auto;
  }

  .projectsContent {
    width: 80%;
  }

  .projectsSquareContainer {
    margin: 5px;
    flex-direction: column;
  }

  .projectsSquare {
    width: 100%;
    height: 220px;
    margin: 15px 0;
  }

  .selectedProjectsContent {
    flex-direction: column;
  }

/*  Portfolio   */

  .projects {
    justify-content: initial;
  }
  .firstColumnSelectedProjects {
    width: 100%;
  }
  .secondColumnSelectedProjects {
    width: 100%;
  }
  .projectCard {
    box-sizing: border-box;
    padding: 15px;
    width: 100%;
    height: 300px;
    margin: 15px 0;
  }

  .projectCardImage {
    height: 70%;
  }

/*   Footer   */
  footer {
    flex-direction: column;
    height: 50vh;
  }

  .footerContent {
    flex-direction: column;
  }

  .firstColumnFooter {
    width: 100%;
  }

  .secondColumnFooter {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: start;
  }
  .secondColumnFooter p {
    width: 60%;
  }

  .footerIconsContainer {
    width: 50%;
  }
}
/*  Project details style   */
.projectDetailsContainer {
    top: 5%;
    left: 5%;
    position: fixed;
    height: 90vh;
    width: 90%;
    background-color: #252734;
    z-index: 2;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
}

.closeDetailsButton {
    position: absolute;
    top: 5%;
    right: 5%;
    background-color: #252734;
    border: solid 1px #ACADB2;
    color: #ACADB2;
    padding: 10px 15px;
    cursor: pointer;
    font-family: 'Open Sans', sans-serif;
    transition: ease-in-out 200ms;
}
.closeDetailsButton:hover {
    border-color: lightcoral;
    color: lightcoral;
}

.projectDetailsContainer h3 {
    color: #EBEBEC;
}

.projectDetailsContainer h2 {
    color: #FFAF29;
}

.projectDetailsContainer p {
    color: #ACADB2;
}

.firstColDetails {
    display: flex;
    flex-direction: column;
    width: 65%;
    height: 90%;
}

.screenshotsContainer {
    width: 100%;
    height: 20%;
}

.secondColDetails {
    display: flex;
    padding: 25px;
    box-sizing: border-box;
    flex-direction: column;
    text-align: start;
    width: 25%;
    height: 90%;
}

.githubLinkDetails {
    margin: 25px 0 0 0;
    text-align: center;
    text-decoration: none;
    width: 150px;
    background-color: #252734;
    border: solid 1px #EBEBEC;
    padding: 10px 15px;
    color: #EBEBEC;
    cursor: pointer;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    transition: ease-in-out 200ms;
}
.githubLinkDetails:hover {
    border-color: #FFAF29;
    color: #FFAF29;
}

@media (max-width: 1060px) {
    .projectDetailsContainer {
        padding: 50px 15px;
        box-sizing: border-box;
        flex-direction: column;
        justify-content: flex-start;
        height: 100vh;
        width: 100%;
        left: 0;
        top: 0;
    }

    .closeDetailsButton {
        top: 1%;
        right: 1%;
    }

    .firstColDetails {
        height: 40%;
        width: 90%;
    }

    .secondColDetails {
        height: 50%;
        width: 90%;
    }

    .secondColDetails h2 {
        margin: 25px 0;
    }

    .secondColDetails h3, .secondColDetails p {
        margin: 5px 0;
    }
}
